@import "../_includes";
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700i,800i,900i');
// +font-face('CenturyGothic', "../fonts/", "CenturyGothic")

html, body {
	font-family: 'Montserrat', sans-serif;
	font-size: 14px {
    font-weight: 500; }
	height: 100%;
	width: 100%;
	@include selection {
		background-color: color-get($mau,main);
		color: #fff; } }

h1, h2, h3, h4, h5, h6 {
	font-size: initial; }

a {
	color: color-get($mau,main);
	&:hover {
		color: color-get($mau,extra); }
	&:visited, &:active {
		color: color-get($mau,main); } }

header {
	width: 100%;
	z-index: 999;
	@include transition(all .5s ease-in-out);
	&.has-tranparent {
		background: none; }
	&.fixedheader {
		@include pos(fix, 0 null null 0); } }

.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
	&:focus {
		@include box-sha(0 0 0 0.2rem rgba(red(color-get($mau,main)),green(color-get($mau,main)),blue(color-get($mau,main)),.25));
		background: color-get($mau,main);
		border-color: color-get($mau,main); } }
.btn, .form-control {
	&:focus {
		@include box-sha(0 0 0 0.2rem rgba(red(color-get($mau,main)),green(color-get($mau,main)),blue(color-get($mau,main)),.25));
		border-color: color-get($mau,main); } }

.btn {
	&.btn-link {
		color: color-get($mau,main); }
	&.btn-primary {
		background: color-get($mau,main);
		border-color: color-get($mau,main); }
	&.btn-outline-primary {
		border-color: color-get($mau,main);
		color: color-get($mau,main);
		&:not(:disabled):not(.disabled) {
			&.active, &:hover {
				background: color-get($mau,main);
				border-color: color-get($mau,main);
				color: color-get($mau,trang); }
			&:focus {
				@include box-sha(0 0 0 0.2rem rgba(red(color-get($mau,main)),green(color-get($mau,main)),blue(color-get($mau,main)),.25)); } } } }

.pagination {
	.page-item {
		.page-link {
			&:focus {
				@include box-sha(0 0 0 0.2rem rgba(red(color-get($mau,main)),green(color-get($mau,main)),blue(color-get($mau,main)),.25)); } }
		&.active {
			.page-link {
				background: color-get($mau,main);
				border-color: color-get($mau,main);
				color: color-get($mau,trang); } } } }
.dropdown-item.active, .dropdown-item:active {
	background: color-get($mau,main); }
